.tarjeta {
    border: 1px solid black;
    padding: 30px;
    border-radius: 20px;
    background-color: white;
    box-shadow: 4px 4px 5px rgba(0,0,0,0.1);
}
.cardbox {
    display: flex;
    flex-flow: row wrap;
    gap: 20px;
    align-items: center;
}
.bandera {
    border-radius: 20px;
}
